.login{
  flex-grow: 1;
  padding: 1.5em;
  @include center;
}

.login-form{
  width: 20em;
  max-width: 100%;
  @include grid100gap(.5em);
  &__logo{
    margin: 0 auto 2em;
    width: 11em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__btn{
    margin-top: .5em;
  }
  &__text{
    margin-top: .5em;
    text-align: right;
  }
}
