.header{
  padding: 1.5em 0;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    width: 11.75em;
    display: block;
    margin-right: 1em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    height: 2.5em;
    display: flex;
    align-items: center;
    border-radius: 0.625em;
    border: 1px solid transparent;
    padding: 0 1.5em;
    @include lastM(0 1em 0 0);
    span{
      font-size: 1em;
      line-height: 100%;
      transform: translateY(5%);
    }
    &._active{
      border-color: #C5D4EF;
    }
  }
}

.burger{
  display: none;
}


@media screen and (max-width: 992px){
  .header{
    padding: 1em 0;
    &__logo{
      width: 8em;
      position: relative;
      z-index: 11;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .burger{
    display: flex;
    align-items: center;
    width: 2.25em;
    height: 2.25em;
    background-color: $primary;
    position: relative;
    z-index: 11;
    border-radius: 50%;
    margin-left: auto;
    svg{
      width: 1.5em;
      height: auto;
      @include centerAbsolute;
    }
    ._view2{
      opacity: 0;
      width: 1.75em;
    }
  }

  .menu{
    position: fixed;
    background-color: #fff;
    z-index: 10;
    width: 100%;
    height: $vh;
    overflow-y: auto;
    top: 0;
    left: 0;
    padding: 5em 1.25em 2em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    &__link{
      padding: 0;
      @include lastM(0 0 .5em);
      border: none;
      &._active{
        color: $primary;
      }
    }
  }
}
