.dashboard{
  padding: 1.5em 0;
}

.position-card,
.indicator-card{
  padding: 1.5em;
  border-radius: 0.625em;
  border: 1px solid #C5D4EF;
}

.indicator-card{
  &__title{
    font-size: .875em;
    line-height: 121%;
  }
  &__nums{
    margin-top: 1em;
    display: flex;
    align-items: center;
  }
  &__value{
    font-size: 3em;
    font-weight: 300;
  }
  &__changes{
    margin-left: 1em;
  }
}

.positions{
  margin-top: 1.38em;
}

.position-card{
  &__top{
    display: flex;
    align-items: center;
  }
  &__icon{
    width: 1.25em;
    height: 1.25em;
    flex-shrink: 0;
    margin-right: .5em;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      border-radius: 50%;
    }
  }
  &__title{
    display: flex;
    align-items: center;
    &:after{
      content: '';
      width: 0.375em;
      height: 0.375em;
      flex-shrink: 0;
      border-radius: 0.125em;
      background: $green;
      margin-left: .5em;
    }
  }
  &__price{
    margin-left: auto;
  }
  &__items{
    max-height: 7.25em;
    margin-top: 1.5em;
    width: calc(100% + 1em);
    padding-right: .5em;
    overflow-y: auto;
    @include grid100gap(1em);
    @media screen and (min-width: 993px){
      @include scroll;
    }
  }
  &__bottom{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: 5.8em 2.45em 3.5em calc(100% - 14em);
    gap: .75em;
    &-col{
      &:nth-child(2){
        text-align: right;
      }
    }
  }
  &__close{
    display: flex;
    align-items: center;
    svg{
      width: .875em;
      height: auto;
      flex-shrink: 0;
      margin-right: .25em;
    }
    &:hover{
      color: $red;
      path{
        fill: $red;
      }
    }
  }
}

.position-indicators{
  margin-top: 1.5em;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.5em 0;
  padding: .5em 0;
  &__item{
    position: relative;
    &:nth-child(odd){
      padding-right: 2.25em;
      &:after{
        content: '';
        position: absolute;
        background: #C5D4EF;
        top: 0;
        right: 2em;
        width: 1px;
        height: 2.5em;
      }
    }
  }
  &__nums{
    margin-top: .75em;
    display: flex;
    align-items: center;
  }
  &__change{
    margin-left: .57em;
  }
}

.position-info{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 1.5em;
  &__buttons{
    display: flex;
    align-items: center;
  }
  &__btn{
    margin-left: .75em;
    width: 1em;
    flex-shrink: 0;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }
}

.position-item{
  display: grid;
  grid-template-columns: 3.8em 4.45em 3.5em calc(100% - 14em);
  gap: .75em;
}


@media screen and (max-width: 992px){
  .dashboard{
    padding: 1em 0;
  }

  .indicator-card{
    padding: 1em;
    &__value{
      font-size: 2.25em;
    }
    &__changes{
      margin-left: .5em;
      .text20{
        font-size: 1.125em;
      }
    }
  }

  .position-card{
    padding: 1em;
    &__items{
      width: calc(100% + .5em);
    }
  }
}
