@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
        line-height: 120%;
    }
}

.m-btn-primary{
    height: 2.75em;
    border-radius: .625em;
    background-color: #66F;
    color: #fff;
    text-transform: uppercase;
    font-weight: 500;
    &:hover{
        background-color: $primary;
        color: #fff;
    }
}
